import { createSlice } from '@reduxjs/toolkit';

const initialState = {
    WorkerPlansData: null,
  errors:[]
};

export const GetWorkerPlanseSlice = createSlice({
  name: 'GetWorkerPlansSlice',
  initialState,
  reducers: {
    GetWorkerPlansSliceInfo: (state, action) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.WorkerPlansData = action.payload;
      console.log(state.WorkerPlansData);
    },
    errors: (state, action) => {
        // Redux Toolkit allows us to write "mutating" logic in reducers. It
        // doesn't actually mutate the state because it uses the Immer library,
        // which detects changes to a "draft state" and produces a brand new
        // immutable state based off those changes
        state.errors = action.payload;
      },
  
  }
});

// Action creators are generated for each case reducer function
export const { GetWorkerPlansSliceInfo ,errors} = GetWorkerPlanseSlice.actions;

export default GetWorkerPlanseSlice.reducer;