import { createSlice } from '@reduxjs/toolkit';

const initialState = {
   EditePersonalCareData: null,
  errors:[]
};

export const EditePersonalCareDataSlice = createSlice({
  name: 'EditePersonalCareDataSlice',
  initialState,
  reducers: {
    EditePersonalCareDataSliceInfo: (state, action) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      // which detects changes to a "draft state" and produces a brand new
      // immutable state based off those changes
      state.EditePersonalCareData = action.payload;
      console.log(state.EditePersonalCareData);
    },
    errors: (state, action) => {
        // Redux Toolkit allows us to write "mutating" logic in reducers. It
        // doesn't actually mutate the state because it uses the Immer library,
        // which detects changes to a "draft state" and produces a brand new
        // immutable state based off those changes
        state.errors = action.payload;
      },
    
  },

  


});

// Action creators are generated for each case reducer function
export const { EditePersonalCareDataSliceInfo ,errors} = EditePersonalCareDataSlice.actions;

export default EditePersonalCareDataSlice.reducer;